.seccion-informacion {
	padding: $separacion 0;
	
	@include tablet {
		@include grid(2, $separacion)
	}

	/* .imagen {

	} */

	.contenido-informacion {

		display: flex;
		flex-direction: column;
		justify-content: center;

		h2 {
			font-size: 3rem;
		}

		.fecha {
			color: $rosa;
			font-weight: $bold;
		}

		.texto {
			line-height: 1.5;
		}

	}
	
}